<template>
  <div class="page-hotel">

    <mt-header title="实惠住宿">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    
    <mt-swipe :auto="4000">
      <mt-swipe-item class="slide1">
        <img src="http://s17.mogucdn.com/mlcdn/c45406/170731_1la3ggjb49b4l4jl06ahkc65k1edj_750x390.jpg" />
      </mt-swipe-item>
      <mt-swipe-item class="slide2">
        <img src="http://s2.mogucdn.com/mlcdn/c45406/170728_4df5le0b5af6af1cg88fjdd87507f_750x390.jpg">
      </mt-swipe-item>
    </mt-swipe>

    <div class="box hotel-info">
      <div class="hotel-name">
        <h3>临平宿舍 <span>空调</span><span>电视</span><span>wifi</span></h3>
        <p>余杭区临平藕花洲大街3号1楼-12楼</p>
        <p>陈大大  15088732260</p>
      </div>
      <div class="hotel-tel" @click="handleTell">
        <a class="common-icon icon-call" tel=""></a>
      </div>
    </div>

    <div class="box">
      <common-title iconClass="icon-house" text="宿舍简介"> </common-title>
      <div class="box-desc">
        <p>1、这里是宿舍简介这里是宿舍简介这里是宿舍简介这里是宿舍简介</p>
        <p>2、这里是宿舍简介这里是宿舍简介这里是宿舍简介这里是宿舍简介这里是宿舍简介</p>
      </div>
    </div>

    <div class="box check-info">
      <h5>入住选择</h5>
      <div class="check-sex">
        <label>性别：</label>

        <span v-for="(item, index) in sex"
            key="index"
            :class="{'on':item.value == sexVal}"
            @click="handleSelect(item)">
              {{item.label}}
        </span>

      </div>
      <div class="check-date"  @click.stop="handleDate">
        <div class="check-in">入住日期 <span>{{begin}}</span></div>
        <div class="check-day">共{{count}}天</div>
        <div class="check-out">退房日期 <span>{{end}}</span></div>
      </div>
    </div>

    <div class="box booking">
      <ul>
        <li>
          <div class="booking-room">
            单人间 
            <span>大床1.8/2.0 20㎡</span>
          </div>
          <div class="booking-price orange"> <span>100</span>元/床</div>
          <mt-button @click.native="handleBooking" plain size="small">
            预定
          </mt-button>
        </li>
        <li>
          <div class="booking-room">
            双人间 
            <span>大床1.8/2.0 20㎡</span>
          </div>
          <div class="booking-price orange"> <span>1580</span>元/床</div>
          <mt-button @click.native="handleBooking" plain size="small">
            预定
          </mt-button>
        </li>
        <li>
          <div class="booking-room">
            4人间 
            <span>大床1.8/2.0 20㎡</span>
          </div>
          <div class="booking-price orange"> <span>60</span>元/床</div>
          <mt-button @click.native="handleBooking" plain size="small">
            预定
          </mt-button>
        </li>
        <li>
          <div class="booking-room">
           6人间 
            <span>大床1.8/2.0 20㎡</span>
          </div>
          <div class="booking-price orange"> <span>40</span>元/床</div>
          <mt-button @click.native="handleBooking" plain size="small">
            预定
          </mt-button>
        </li>
      </ul>
    </div>
    
  </div>
</template>

<script>
import commonTitle from '../../common/title.vue'

export default {
  name: 'page-hotel',

  components: {
    commonTitle
  },
  data() {
    return {
      sexVal: '1',
      begin: '7月3日',
      end:'7月9日',
      count: '6'
    };
  },

  created() {
    this.sex = [
      {
        label: '男',value: '1'
      },
      {
        label: '女',value: '0'
      }
    ]

    let data = localStorage.getItem("booking-date");
    if(data && data != ""){
      let bookingDate = JSON.parse(data);

      if(this._isObj(bookingDate)){
        this.begin = bookingDate.begin;
        this.end = bookingDate.end;
        this.count = bookingDate.count;
      }
    }
  },



  destroyed () {
    localStorage.setItem("booking-date", ""); //清除日期存储
  },

  methods: {
    handleDate () {
      this.$router.push({ path: 'date', query: { refer: 'post' }})
    },

    handleSelect (item) {
      this.sexVal = item.value;
    },

    handleTell () {
      location.href = 'tel: 18329169978';
    },

    handleBooking () {
        this.$router.push({ path: 'userInfo', query: { refer: 'post' }})
    },
    
    _isObj (val){
      return Object.prototype.toString.call(val) === '[object Object]'
    }
  }
};
</script>

<style lang="css">
  .mint-swipe {
    height: 170px;
    overflow:hidden;
  }
  .mint-swipe-item img{
    width:100%;
  }


  .hotel-info{
    position: relative;
    margin-bottom:10px;
    padding:10px 15px;
    background:#fff;
  }
  .hotel-info h3{
    display: flex;
    align-items:center;
    margin: 5px 0 10px;
    font-size:17px;
  }
  .hotel-info span{
    margin-left:8px;
    padding:0 5px;
    height:16px;
    font-size:12px;
    font-weight:400;
    color:#0977C5;
    background: #FFFFFF;
    border: 1px solid #0977C5;
    border-radius: 2px;
  }

  .hotel-info p{
    margin:5px 0;
    max-width:270px;
    font-size: 15px;
    color: #828282;
  }

  .hotel-info .hotel-tel {
    position: absolute;
    right: 10px;
    top: 20px;
    width: 60px;
    height: 70px;
    border-left: 1px solid #E9E9E9;
  }

  .hotel-info .icon-call{
    width: 60px;
    height: 70px;
    background:url(../../../images/call_icon@3x.png) 15px 20px no-repeat;
    background-size: 26px auto;
  }


  .check-info{
    margin-top:-11px;
  }
  .check-info h5{
    margin:0;
    padding:10px 15px;
    background: #f0f0f0;
    font-size: 15px;
    color: #828282;
    font-weight:400;
  }
  .check-info .check-sex{
    display:flex;
    align-items:center;
    padding:15px;
    font-size:15px;
    color: #828282;
    border-bottom: 1px solid #E9E9E9;
  }
  .check-info .check-sex span{
    margin:0 6px;
    padding:6px 25px;
    background: #fff;
    border: 1px solid #D7D7D7;
    border-radius: 4px;
  }
  .check-info .check-sex span.on{
    background: #0977C5;
    border: 1px solid #0977C5;
    color:#fff;
  }

  .booking >ul>li{
    display:flex;
    justify-content: space-between;
    align-items: center;
    padding:15px;
    border-bottom: 1px solid #E9E9E9;
  }
  .booking-room{
    width:50%;
    line-height:24px;
    font-size: 17px;
    color: #1E1E1E;
  }
  .booking-room span{
    display:block;
    font-size: 15px;
    color: #828282;
  }
  .booking-price{
    width:26%;
    font-size:14px;
    text-align:right;
  }
  .booking-price span{
    font-size:18px;
  }
  .booking >ul>li> .mint-button{
    padding: 0 15px;
    border: 1px solid #0977C5;
    color: #0977C5;
  }

</style>


