<template>
  <div class="page-booking">

    <mt-header title="实惠住宿">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    
    <div class="userinfo-group">
      <h3 class="group-hd">完善个人信息</h3>
      <div class="group-bd userinfo-mint">
        <mt-field class="mint-username" label="入 住 人：" placeholder="入住人">
        </mt-field>
        <mt-field class="mint-telphone" label="手机号码：" placeholder="手机号码" type="tel"></mt-field>
        <mt-field class="mint-idnumber" label="身份证号：" placeholder="身份证号"></mt-field>
      </div>
    </div>

    <div class="userinfo-group">
      <h3 class="group-hd">选择入住类型</h3>
      <div class="group-bd">
        <mt-radio
            align="right"
            class="page-part"
            v-model="value"
            :options="options" />
      </div>
    </div>

    <div class="fix-bottom">
      <mt-button @click.native="handleNext" size="large" type="primary">下一步</mt-button>
    </div>
    
  </div>
</template>

<script>
import '../../css/process.css';
import '../../css/booking.css';

export default {
  name: 'page-booking-userinfo',

  components: {
  },
  
  data() {
    return {
      value: '1'
    };
  },

  created() {
    this.options = [
      {
        label: '培训入住',
        value: '1'
      },
      {
        label: '工作入住',
        value: '2'
      },
      {
        label: '其他',
        value: '3'
      }
    ];
  },

  methods: {
    handleNext: function() {
      this.$router.push({ path: 'house', query: { plan: 'private' }})
    }
  }
};
</script>

<style lang="css">
  .userinfo-group {
    margin: 0 0 10px;
  }

  .userinfo-group .group-hd{
    margin:10px 0;
    padding:0 15px;
    font-size: 15px;
    font-weight:400;
    color: #828282;
  }

</style>


