<template>
  <div class="page-order-list">

    <mt-header title="我的订单">
      <router-link :to="'/'" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>


    <mt-navbar class="page-part" v-model="selected">
      <mt-tab-item id="1">待支付</mt-tab-item>
      <mt-tab-item id="2">使用中</mt-tab-item>
      <mt-tab-item id="3">已完成</mt-tab-item>
    </mt-navbar>

    <mt-tab-container v-model="selected">

      <mt-tab-container-item id="1">
        <div class="order-infinite-wrapper" ref="wrapper1" :style="{ height: wrapperHeight1 + 'px' }">
          <ul class="order-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="50">
            <order-item 
                v-for="(item, index) in list1"
                key="index"
                href="detail?id=123"
                hotel="天上人间宿舍"
                room="A201-四人间-3个床位" 
                time="2017.05.24-2017.06.23 共30天"
                price="¥4500"
                btnTxt="去支付" />
          </ul>
          <p v-show="loading" class="order-infinite-loading">
            <mt-spinner type="fading-circle"></mt-spinner>
            加载中...
          </p>
        </div>
      </mt-tab-container-item>

      <mt-tab-container-item id="2">
        <div class="order-infinite-wrapper" ref="wrapper2" :style="{ height: wrapperHeight2 + 'px' }">
          <ul class="order-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="50">
            <order-item 
                v-for="(item, index) in list2"
                key="index"
                href="detail?id=123"
                hotel="海底捞员工宿舍"
                room="A301-四人间-3个床位" 
                time="2017.05.24-2017.06.23 共30天"
                price="¥1500"
                btnTxt="续订" />
          </ul>
          <p v-show="loading" class="order-infinite-loading">
            <mt-spinner type="fading-circle"></mt-spinner>
            加载中...
          </p>
        </div>
      </mt-tab-container-item>

      <mt-tab-container-item id="3">
        <div class="order-infinite-wrapper" ref="wrapper2" :style="{ height: wrapperHeight2 + 'px' }">
          <ul class="order-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="50">
            <order-item 
                v-for="(item, index) in list2"
                key="index"
                href="detail?id=123"
                hotel="新白鹿员工宿舍"
                room="A401-四人间-3个床位" 
                time="2017.05.24-2017.06.23 共30天"
                price="¥1000"
                btnTxt="续订" />
          </ul>
          <p v-show="loading" class="order-infinite-loading">
            <mt-spinner type="fading-circle"></mt-spinner>
            加载中...
          </p>
        </div>
      </mt-tab-container-item>
    </mt-tab-container>

  </div>
</template>

<script>
import orderItem from '../../common/orderItem.vue'

export default {
  name: 'page-order-list',

  components: {
      orderItem
  },

  data() {
    return {
      selected: '1',
      list1: [],
      list2: [],
      list3: [],
      loading: false,
      allLoaded: false,
      wrapperHeight1: 0,
      wrapperHeight2: 0
    };
  },

  methods: {
    back () {
      window.history.back()
    },
    loadMore() {
      this.loading = true;
      setTimeout(() => {

        let last1 = this.list1[this.list1.length - 1];
        let last2 = this.list2[this.list2.length - 1];
        for (let i = 1; i <= 5; i++) {
          this.list1.push(last1 + i);
          this.list2.push(last2 + i);
        }
        this.loading = false;
      }, 1000);
    }
  },

  mounted() {
    this.wrapperHeight1 = document.documentElement.clientHeight - this.$refs.wrapper1.getBoundingClientRect().top;
    this.wrapperHeight2 = document.documentElement.clientHeight - this.$refs.wrapper2.getBoundingClientRect().top;
    for (let i = 1; i <= 5; i++) {
      this.list1.push(i);
      this.list2.push(i);
    }
  }
};
</script>

<style lang="css">
  .page-order-list{

  }

  .mint-navbar{
    border: 1px solid #E9E9E9;
  }
  .mint-navbar .mint-tab-item{
    padding:15px 0;
  }
  .mint-navbar .mint-tab-item.is-selected{
    color: #0977C5;
    border-color:#0977C5;
    margin-bottom:0;
  }
  .mint-tab-item-label {
    font-size: 16px;
  }


  .order-infinite-wrapper {
    margin-top: -1px;
    overflow: scroll;
  }


  .order-infinite-loading {
    text-align: center;
    height: 50px;
    line-height: 50px;
  }
  .order-infinite-loading div {
    display: inline-block;
    vertical-align: middle;
    margin-right: 5px;
  }

</style>


