<template>
  <div class="page-map">

    <mt-header title="实惠住宿">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header> 

    <nav class="tab-city" ref="tabCity">
      <mt-button @click.native="showPopup('province')" 
                size="large" 
                :class="{'active':popupStatus == 'province' && popupVisible}"
                >
        {{province}}
      </mt-button>
      <span class="tab-line"></span>
      <mt-button @click.native="showPopup('city')" 
                size="large"
                :class="{'active':popupStatus == 'city' && popupVisible}"
                >
        {{city}}
      </mt-button>

      <mt-popup v-model="popupVisible" popup-transition="popup-fade" class="city-popup" :style="{ top: buttonBottom + 'px' }">
        <ul>
          <li v-if="popupStatus == 'province'" 
              @click="selectProvince(item)"
              :class="{'active':item == province}"
              v-for="(item, index) in provinceList">
            {{item}}
          </li>
          <li v-if="popupStatus == 'city'"  
              @click="selectCity(item)"
              :class="{'active':item == city}"
              v-for="(item, index) in cityList">
            {{item}}
          </li>
        </ul>
      </mt-popup>
    </nav>

    <div id="allmap" class="map-wrap" ref="mapWrap" :style="{ height: mapHeight + 'px' }"></div>

    <div class="fix-bottom fix-map">
      <mt-button @click.native="pointVisible = true">
        本地区共有<span>5</span>个住宿点
      </mt-button>
      <mt-popup v-model="pointVisible" position="bottom" class="hotel-popup">
        <mt-button @click.native="pointVisible = false">
          本地区共有<span>5</span>个住宿点
        </mt-button>
        <div class="list-hotel-popup">
          <div class="hotel-item" @click="handleHotel(123)" >
            <h3>临平宿舍 <span>空调</span><span>电视</span><span>wifi</span></h3>
            <p>余杭区临平藕花洲大街3号1楼-12楼</p>
            <p>陈大大  15088732260</p>
          </div>
          <div class="hotel-item" @click="handleHotel(456)">
            <h3>余杭宿舍 <span>空调</span><span>电视</span><span>wifi</span></h3>
            <p>余杭区临平藕花洲大街3号1楼-12楼</p>
            <p>陈大大  15088732260</p>
          </div>
          <div class="hotel-item" @click="handleHotel(789)">
            <h3>萧山宿舍 <span>空调</span><span>电视</span><span>wifi</span></h3>
            <p>余杭区临平藕花洲大街3号1楼-12楼</p>
            <p>陈大大  15088732260</p>
          </div>
          <div class="hotel-item" @click="handleHotel(911)">
            <h3>临平宿舍 <span>空调</span><span>电视</span><span>wifi</span></h3>
            <p>余杭区临平藕花洲大街3号1楼-12楼</p>
            <p>陈大大  15088732260</p>
          </div>
        </div>
      </mt-popup>
    </div> 
    
  </div>
</template>


<script>
import address from '../common/address.js'
import '../css/city.css';

// 自定义地图覆盖物
function mapOverlay(point, text){
  this._point = point;
  this._text = text;
}
mapOverlay.prototype = new BMap.Overlay();
mapOverlay.prototype.initialize = function(map){
  this._map = map;
  let div = this._div = document.createElement("div");
  div.style.position = "absolute";
  div.style.zIndex = BMap.Overlay.getZIndex(this._point.lat);
  div.style.height = "60px";
  div.style.width="200px";

  let arrow = this._arrow = document.createElement("img");
  arrow.src = "http://www.cssoul.com/demo/weihai/images/place_icon@3x.png";
  arrow.style.width = "50px";
  arrow.style.height = "50px";
  div.appendChild(arrow);

  let text = document.createElement("div");
  text.style.height = "25px";
  text.style.padding = "0 6px";
  text.style.position = "absolute";
  text.style.left = "50px";
  text.style.top = "5px";
  text.style.lineHeight="25px";
  text.style.color="#FFFFFF";
  text.style.textAlign="center";
  text.style.background="#F9621D";
  text.style.border="1px solid #FFFFFF";
  text.style.borderRadius = '4px';
  text.innerHTML = this._text;
  div.appendChild(text);
 
  map.getPanes().labelPane.appendChild(div);
  
  return div;
}

mapOverlay.prototype.draw = function(){
  var map = this._map;
  var pixel = map.pointToOverlayPixel(this._point);
  this._div.style.left = pixel.x -25 + "px";
  this._div.style.top  = pixel.y -45 +"px";
}

export default{
  name: 'page-map',

  data : function(){
    return{
      provinceList:[],
      cityList:[],
      address: ['浙江', '杭州'],
      province: '浙江',
      city: '杭州',
      popupVisible: false,
      popupStatus: 'province',
      buttonBottom: 0,
      pointVisible: false,
      mapHeight:500
    }
  },
  props: {
    // 经度
    longitude:{
      type:Number,
      default:116.404
    },
    // 纬度
    latitude:{
      type:Number,
      default:39.915
    },

    description:{
      type:String,
      default:'天安门'
    }
  },

  methods: {
    showPopup (type) {
      this.popupStatus = type;  //切换省、城市
      
      this.popupVisible = true;
    },
    
    selectProvince (item) {
      this.province = item;
      this.cityList = address[item];
      this.city = address[item][0];   //默认选中第一个

      this.address = [this.province, this.city];

      this.popupVisible = false;
    },

    selectCity (item) {
      this.city = item;
      this.popupVisible = false;
    },

    ready () {
      //创建地图实例
      let map = new BMap.Map("allmap");
      //中心坐标、地图级别
      map.centerAndZoom(new BMap.Point(120.168107,30.263516), 13);
      //操作
      map.enableScrollWheelZoom(true);
      map.enableDoubleClickZoom(true);

      var overlay1 = new mapOverlay(new BMap.Point(120.167918,30.262908),'佳丽住宿');
      var overlay2 = new mapOverlay(new BMap.Point(120.148612,30.305328),'临平住宿');

      map.addOverlay(overlay1);
      map.addOverlay(overlay2);
    },

    handleHotel (id) {
      this.$router.push({ path: '/booking/hotel', query: { id: id}})
    }
  },

  created() {
    this.provinceList = Object.keys(address);
    this.cityList = address[this.province];
  },

  mounted() {
    this.ready();

    setTimeout(() => {
      this.mapHeight = document.documentElement.clientHeight - this.$refs.mapWrap.getBoundingClientRect().top;
      this.buttonBottom = this.$refs.tabCity.getBoundingClientRect().bottom;
    }, 100);
  }

}
</script>

<style lang="css">
  .fix-map .mint-button--normal{
    width:100%;
    background:url(../../images/down_icon@3x.png) #fff 80% 20px no-repeat;
    background-size: 12px auto;
  }

  .fix-map .mint-button--normal span{
    color: #0977C5;
  }
  .fix-map .mint-button-text{
    margin-left:-20px;
  }

  .map-wrap{
    width:100%;
  }
  .list-hotel-popup{
    margin-top: 1px;
    max-height: 300px;
    overflow-y: scroll;
  }
  .hotel-item{
    padding:10px 15px;
    background:#fff;
    border-bottom: 1px solid #E9E9E9;
  }
  .hotel-item h3{
    display: flex;
    align-items:center;
    margin: 5px 0 10px;
    font-size:17px;
    color: #1E1E1E;
  }
  .hotel-item p{
    font-size:15px;
  }
  .hotel-item span{
    margin-left:8px;
    padding:0 5px;
    height:16px;
    font-size:12px;
    font-weight:400;
    color:#0977C5;
    background: #fff;
    border: 1px solid #0977C5;
    border-radius: 2px;
  }

  .hotel-popup{
    width: 100%;
  }
  .hotel-popup .mint-button--normal{
    background:url(../../images/up_icon_grey@3x.png) #fff 80% 20px no-repeat;
    background-size: 12px auto;
  }
 
</style>


