<template>
  <div class="page-renew">

    <mt-header title="我要续订">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <div class="box check-info">
      <div class="check-date"  @click.stop="handleDate">
        <div class="check-in">入住日期 <span>{{begin}}</span></div>
        <div class="check-day">共{{count}}天</div>
        <div class="check-out">退房日期 <span>{{end}}</span></div>
      </div>
    </div>

    <div class="fix-bottom">
      <mt-button @click.native="handleRenew" size="large" type="primary">提交订单</mt-button>
    </div>
    
  </div>
</template>

<script>

export default {
  name: 'page-renew',

  components: {

  },

  data() {
    return {
      begin: '7月3日',
      end:'7月9日',
      count: '6'
    };
  },

  created() {
    let data = localStorage.getItem("booking-date");
    if(data && data != ""){
      let bookingDate = JSON.parse(data);

      if(this._isObj(bookingDate)){
        this.begin = bookingDate.begin;
        this.end = bookingDate.end;
        this.count = bookingDate.count;
      }
    }
  },

  destroyed () {
    localStorage.setItem("booking-date", ""); //清除日期存储
  },

  methods: {
    handleDate () {
      this.$router.push({ path: 'date', query: { refer: 'post' }})
    },

    handleRenew () {
      let msg = '您的房间<span>'+this.begin+'<\/span>至<span>'+this.end+'<\/span>已经被人预定走喽，请修改时间'

      this.$messagebox({
        title: '档期已满',
        message: msg,
        type:'confirm',
        showCancelButton: true,
        confirmButtonText: '不续订了',
        cancelButtonText: '修改时间',
      }).then(action => {
        if(action == "confirm"){
          window.history.back();
        }
      });

    },

    _isObj (val){
      return Object.prototype.toString.call(val) === '[object Object]'
    }
  }
};
</script>



