<template>
  <div class="page-booking">

    <mt-header title="选择床位">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <div class="box">
      <div class="house-title">
        <i class="common-icon icon-house"></i>
        <span class="house-title-text">选择房间</span>

        <span class="house-title-choose">已选：{{houseName}}</span>
      </div>

      <div class="house-list">
        <mt-button
            v-for="(item, index) in houses"
            key="index"
            :class="{'primary':item.value == houseVal}"
            size="small"
            @click.native="handleHouse(item)">
              {{item.label}}
        </mt-button>
      </div>
    </div>

    <div class="box">
      <div class="house-title">
        <i class="common-icon icon-bed"></i>
        <span class="house-title-text">选择床位</span>

        <span class="house-title-hasbed"><i class="circle-gray"></i>床位可选</span>
      </div>

      <div class="bed-list">
        <table>
          <thead>
            <tr>
              <td>床位</td>
              <td>下铺</td>
              <td>中铺</td>
              <td>上铺</td>
            </tr>
          </thead>
          <tbody>

            <tr v-for="(item, index) in bed" key="index">
              <td>{{item.label}}</td>
              <td>
                <span v-if="item.down.state == 0" class="gray">无</span>
                <span v-else-if="item.down.state == 2" class="orange">已满</span>
                <span v-else>
                  <label :for='"wh-"+item.down.id'>
                  <input :id='"wh-"+item.down.id' class="mint-radio-input" type="checkbox" v-model='bedList' :value="item.down.id" />
                  <span class="mint-radio-core"></span>
                  </label>
                </span>
              </td>
              <td>
                <span v-if="item.middle.state == 0" class="gray">无</span>
                <span v-else-if="item.middle.state == 2" class="orange">已满</span>
                <span v-else>
                  <label :for='"wh-"+item.middle.id'>
                  <input :id='"wh-"+item.middle.id' class="mint-radio-input" type="checkbox" v-model='bedList' :value="item.middle.id" />
                  <span class="mint-radio-core"></span>
                  </label>
                </span>
              </td>
              <td>
                <span v-if="item.up.state == 0" class="gray">无</span>
                <span v-else-if="item.up.state == 2" class="orange">已满</span>

                  <label v-else :for='"wh-"+item.up.id'>
                  <input :id='"wh-"+item.up.id' class="mint-radio-input" type="checkbox" v-model='bedList' :value="item.up.id" />
                  <span class="mint-radio-core"></span>
                  </label>

              </td>
            </tr>

          </tbody>
        </table>
      </div>
    </div>

    <div class="fix-bottom fix-order">
      <div class="order-summary">
        <span class="order-price">合计：¥2320</span>
        <span class="order-extra">共29天  单人间  1个床位</span>
      </div>
      <mt-button @click.native="handleNext" size="small" type="primary">下一步</mt-button>
    </div>

  </div>
</template>

<script>
import '../../css/booking.css';

export default {
  name: 'page-booking-bed',

  components: {

  },
  data() {
    return {
      houseVal: '4',
      houseName: 'A204',
      bedList:[]
    };
  },

  created() {
    this.houses = [
      {
        label: 'A201',value: '1'
      },
      {
        label: 'A202',value: '2'
      },
      {
        label: 'A203',value: '3'
      },
      {
        label: 'A204',value: '4'
      },
      {
        label: 'A205',value: '5'
      },
      {
        label: 'A206',value: '6'
      },
      {
        label: 'A207',value: '7'
      },
      {
        label: 'A208',value: '8'
      },
      {
        label: 'A209',value: '9'
      },
      {
        label: 'A210',value: '10'
      }
    ];

    this.bed = [
      {
        label: '1号床',
        down: {'id':'111',state:0},
        middle: {'id':'333',state:1},
        up: {'id':'222',state:2}
      },
      {
        label: '2号床',
        down: {'id':'123',state:1},
        middle: {'id':'234',state:1},
        up: {'id':'456',state:1}
      },
      {
        label: '3号床',
        down: {'id':'1234',state:0},
        middle: {'id':'2345',state:0},
        up: {'id':'3456',state:1}
      },
      {
        label: '4号床',
        down: {'id':'12345',state:2},
        middle: {'id':'23456',state:1},
        up: {'id':'34567',state:0}
      }
    ]
  },

  methods: {
    handleHouse (item) {
      this.houseVal = item.value;
      this.houseName = item.label;
    },

    handleNext () {
      console.log('选中床位:' + this.bedList.join(','));

      this.$router.push({ path: '/order/place', query: { plan: 'private' }})
    }
  }
};
</script>

<style lang="css">
  .house-title-hasbed{
    font-size:15px;
    color: #828282;
  }
  .circle-gray{
    display:inline-block;
    width:12px;
    height:12px;
    margin:0 3px;
    border-radius:50%;
    border:1px solid #0977C5;
  }


  .bed-list{
    min-height:200px;
    padding:15px;
  }
  .bed-list table {
    clear: both;
    width: 100%;
    margin-bottom:10px;
    border-collapse: collapse;
    color: #828282;
    font-size:15px;
  }
  .bed-list table thead{
    background: #F0F0F0;
    box-shadow: 0 -1px 0 0 #D7D7D7, 0 1px 0 0 #D7D7D7;
  }
  .bed-list table tbody td{
    height:50px;
  }

  .bed-list table td {
    width: 25%;
    height: 44px;
    text-align: center;
    vertical-align: middle;
    border-right:1px solid #D7D7D7;
  }
  .bed-list table tr td:last-child{
    border-right:0;
  }
  .bed-list table tr{
    border-bottom:1px solid #D7D7D7
  }

</style>


