<template>
  <div class="page-process">

    <mt-header title="注册">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>

    <div class="process-group userinfo-mint">
      <mt-field class="mint-tel" placeholder="手机号码" v-model="telphone" type="tel"></mt-field>
      <mt-field class="mint-code" placeholder="输入验证码" v-model="verifyCode">
        <mt-button size="small" @click.native="handleCode" type="primary">获取验证码</mt-button>
      </mt-field>
    </div>

    <div class="process-group process-handle">
      <mt-button size="large" @click.native="handleLogin" type="primary">注册</mt-button>
    </div>

  </div>
</template>

<script>
import '../../css/process.css';

export default {
  name: 'page-register',

  components: {

  },

  data () {
    return {
      telphone: '',
      verifyCode: ''
    }
  },

  methods: {

    handleCode () {
      var isTel = this.checkTel();

      if(isTel){
        //todo 验证码
      }
    },

    handleLogin () {
      
      var isTel = this.checkTel();

      if(isTel){
        if(!this.verifyCode){
          this.$toast("请输入验证码");
        }else{
          //todo 登录
        }
      }
    },

    checkTel (){
      if (!this.telphone) {
        this.$toast("请输入手机号码");
        return false;
      }else if (!/^[0-9]{11}$/.test(this.telphone)) {
        this.$toast("手机号码格式错误");
        return false;
      }else{
        return true;
      }
    }
  }

};
</script>

