// 自定义地图覆盖物
function mapOverlay(point, text) {
  this._point = point;
  this._text = text;
}
mapOverlay.prototype = new BMap.Overlay();
mapOverlay.prototype.initialize = function (map) {
  this._map = map;
  var div = this._div = document.createElement("div");
  div.style.position = "absolute";
  div.style.zIndex = BMap.Overlay.getZIndex(this._point.lat);
  div.style.height = "60px";
  div.style.width = "200px";

  var arrow = this._arrow = document.createElement("img");
  arrow.src = "http://www.cssoul.com/demo/weihai/images/place_icon@3x.png";
  arrow.style.width = "50px";
  arrow.style.height = "50px";
  div.appendChild(arrow);

  var text = document.createElement("div");
  text.style.height = "25px";
  text.style.padding = "0 6px";
  text.style.position = "absolute";
  text.style.left = "50px";
  text.style.top = "5px";
  text.style.lineHeight = "25px";
  text.style.color = "#FFFFFF";
  text.style.textAlign = "center";
  text.style.background = "#F9621D";
  text.style.border = "1px solid #FFFFFF";
  text.style.borderRadius = '4px';
  text.innerHTML = this._text;
  div.appendChild(text);

  map.getPanes().labelPane.appendChild(div);

  return div;
};

mapOverlay.prototype.draw = function () {
  var map = this._map;
  var pixel = map.pointToOverlayPixel(this._point);
  this._div.style.left = pixel.x - 25 + "px";
  this._div.style.top = pixel.y - 45 + "px";
};

